@charset "UTF-8";
/* GoogleFont */
@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;500;600;700&family=Plus+Jakarta+Sans:wght@400;500;600;700&family=JetBrains+Mono:wght@400;500&display=swap");
/* Tailwindcss v4 */
@import "tailwindcss/theme.css" layer(theme);
@import "tailwindcss/preflight.css" layer(base);
@import "tailwindcss/utilities.css";
/* Tailwindcss Typography */
@import "@spartan-ng/brain/hlm-tailwind-preset.css";
@plugin "@tailwindcss/typography";
/* Spartan */
/* Tailwindcss Layers */
@layer theme, base, components, utilities;
@theme {
  /* Font Tanımları */
  --font-display: "Outfit", sans-serif;
  --font-body: "Plus Jakarta Sans", sans-serif;
  --font-mono: "JetBrains Mono", monospace;
  /* Renk Eşleşmeleri - OKLCH Kullanımı */
  --color-background: var(--background);
  --color-foreground: var(--foreground);
  --color-primary: var(--primary);
  --color-primary-foreground: var(--primary-foreground);
  --color-secondary: var(--secondary);
  --color-secondary-foreground: var(--secondary-foreground);
  --color-muted: var(--muted);
  --color-muted-foreground: var(--muted-foreground);
  --color-accent: var(--accent);
  --color-accent-foreground: var(--accent-foreground);
  --color-destructive: var(--destructive);
  --color-destructive-foreground: var(--destructive-foreground);
  --color-border: var(--border);
  --color-input: var(--input);
  --color-ring: var(--ring);
  --radius-xl: 0.75rem;
  --radius-lg: 0.5rem;
  --radius-md: 0.25rem;
  --radius-sm: 0.125rem;
}
@layer base {
  :root {
    /* Light Mode - OKLCH */
    /* oklch(L C H) -> L: Parlaklık, C: Kroma (Canlılık), H: Hue (Renk Tonu) */
    --background: oklch(0.96 0.01 210);
    --foreground: oklch(0.25 0.04 222);
    --primary: oklch(0.55 0.18 260);
    --primary-foreground: oklch(0.98 0.01 210);
    --secondary: oklch(0.92 0.02 210);
    --secondary-foreground: oklch(0.25 0.04 222);
    --muted: oklch(0.90 0.02 210);
    --muted-foreground: oklch(0.55 0.03 215);
    --accent: oklch(0.70 0.15 190);
    --accent-foreground: oklch(0.25 0.04 222);
    --destructive: oklch(0.60 0.20 25);
    --destructive-foreground: oklch(0.98 0.01 210);
    --border: oklch(0.88 0.02 210);
    --bg-grid-border: oklch(74.217% 0.13411 220.449);
    --input: oklch(76.55% 0.14001 221.029);
    --ring: oklch(0.55 0.18 260);
    --cursor-outline-color: oklch(47.478% 0.00005 271.152);
    --resume-teal: #4b8b98;
    --resume-radius: 10px;
  }
  .dark {
    /* Dark Mode - OKLCH */
    --background: oklch(0.18 0.02 224);
    --foreground: oklch(0.98 0.01 210);
    --primary: oklch(0.65 0.20 260);
    --primary-foreground: oklch(0.98 0.01 210);
    --secondary: oklch(0.28 0.03 217);
    --secondary-foreground: oklch(0.98 0.01 210);
    --muted: oklch(0.25 0.03 217);
    --muted-foreground: oklch(0.70 0.03 215);
    --accent: oklch(0.75 0.15 190);
    --accent-foreground: oklch(0.18 0.02 224);
    --destructive: oklch(0.45 0.18 25);
    --destructive-foreground: oklch(0.98 0.01 210);
    --border: oklch(0.28 0.03 217);
    --input: oklch(0.28 0.03 217);
    --ring: oklch(0.65 0.20 260);
  }
  * {
    @apply border-border outline-ring/50;
  }
  html, body {
    cursor: url("/asset/icon/arrow.svg") 9 0, auto;
  }
  .cursor-dot, .cursor-outline {
    position: fixed;
    top: 0;
    left: 0;
    pointer-events: none;
    z-index: 9999;
    opacity: 0;
    will-change: transform;
    transition: opacity 0.3s ease;
  }
  .cursor-dot {
    width: 8px;
    height: 8px;
    transition: transform 0.07s cubic-bezier(0.25, 0.1, 0.25, 1), opacity 0.3s ease;
  }
  .cursor-dot-inner {
    width: 100%;
    height: 100%;
    background-color: var(--primary);
    border-radius: 50%;
    opacity: 0.7;
  }
  .cursor-outline {
    width: 40px;
    height: 40px;
  }
  .cursor-outline-inner {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 1px solid var(--cursor-outline-color);
    transition: transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275), background-color 0.3s ease;
  }
  .cursor-hover .cursor-dot-inner {
    animation: cursor-pulse 1.5s infinite ease-in-out;
  }
  .cursor-hover .cursor-outline-inner {
    transform: scale(1.5);
    background-color: rgba(175, 113, 255, 0.1);
    animation: cursor-pulse 2.2s cubic-bezier(0.4, 0, 0.2, 1) infinite;
  }
  @keyframes cursor-pulse {
    0% {
      transform: scale(1);
      opacity: 0.7;
    }
    50% {
      transform: scale(1.45);
      opacity: 0.3;
    }
    100% {
      transform: scale(1);
      opacity: 0.7;
    }
  }
  /* Mobile*/
  @media (hover: none) and (pointer: coarse) {
    .cursor-dot,
    .cursor-outline {
      display: none !important;
    }
    html, body {
      cursor: auto !important;
    }
  }
  h1, h2, h3, h4, h5, h6 {
    font-family: var(--font-display);
    @apply font-bold tracking-tight;
  }
  code, pre {
    font-family: var(--font-mono);
  }
}
@layer components {
  .glass-panel {
    @apply bg-background/60 backdrop-blur-xl border border-white/10;
  }
}
@layer utilities {
  .text-glow {
    text-shadow: 0 0 20px hsla(var(--primary), 0.5);
  }
  .bg-grid-pattern {
    background-size: 40px 40px;
    background-image: linear-gradient(to right, var(--bg-grid-border) 1px, transparent 1px), linear-gradient(to bottom, var(--bg-grid-border) 1px, transparent 1px);
  }
}
